import React from "react";
import styled from 'styled-components';
import homeBackground from "../images/logo.jpg";
import DefaultLayout from "../components/layout/DefaultLayout";
import HomeBackground from "../components/common/HomeBackground";
import MaskDark from "../components/common/MaskDark";
import Title from "../components/common/Title";
import FontFamily from "../components/common/FontFamily";
import Middle from "../components/common/Middle";

const ContentSize = styled.div`
	width: 100%;
`;
const OneHalf = styled.div`
    margin: 10px 185px 0px 185px;
    width: 75%;
    display: flex;
`;
const Column = styled.div`
    width: 50%;
    height: 100%;
    margin: 10px;
    padding: 10px;
    background-color: #F5F5F5;
    border-radius: 10px;
`;

const ExperiencePage = () => {
    return (
        <DefaultLayout>
            <HomeBackground url={homeBackground}>
                <MaskDark>
                    <Middle>Experience</Middle>
                </MaskDark>
            </HomeBackground>
            <ContentSize>
                <FontFamily>
                    <Title></Title>
                    <OneHalf>
                        <Column> (C++)</Column>
                        <Column> (JavaPythonHtmlCSSJavaJavaScript)</Column>
                    </OneHalf>
                    <Title></Title>
                    <OneHalf>
                        <Column> </Column>
                        <Column> (TA)</Column>
                    </OneHalf>
                    <Title></Title>
                    <OneHalf>
                        <Column> (TA)React</Column>
                        <Column> (TA)</Column>
                    </OneHalf>
                    <Title></Title>
                    <OneHalf>
                        <Column> (TATA)</Column>
                        <Column> (TATA)</Column>
                    </OneHalf>
                </FontFamily>
            </ContentSize>
        </DefaultLayout>
    )
};

export default ExperiencePage;